import { Component, OnInit, ElementRef, ViewChild, ChangeDetectorRef } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Validations } from '../../shared/validators/Validations';
import { ToastrService } from 'ngx-toastr';
import { ChangePasswordService } from '../../shared/services/change-password.service';
import { Route } from '@angular/compiler/src/core';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
})
export class ChangePasswordComponent implements OnInit {
  
  changePasswordForm: FormGroup;
  submitted: boolean = false;
  passmessage: string = "";

  constructor(
    private titleService: Title,
    private changePasswordService:ChangePasswordService,
    private toastr: ToastrService,
    private router:Router
  ) {}

  ngOnInit(): void {
    this.titleService.setTitle("Change Password");

    this.changePasswordForm = new FormGroup({
      current_password: new FormControl('', [
        Validators.required,
        Validators.minLength(6)
      ]),
      new_password: new FormControl('', [
        Validators.required,
        Validators.minLength(6)
      ]),
      new_confirm_password: new FormControl('', [
        Validators.required,
        Validators.minLength(6)
      ])
    });
  }

  checkPassword() {
    let new_password = this.changePasswordForm.controls.new_password.value;
    let new_confirm_password = this.changePasswordForm.controls.new_confirm_password.value;
    if (new_confirm_password != new_password && this.changePasswordForm.valid == true) {
      this.passmessage = "New password and new confirm password must be same.";
      return true;
    }
    else {
      this.passmessage = "";
      return false;
    }
  }

  submitChangePasswordForm(){
    this.submitted = true;

      var formData = new FormData();
      formData.append("current_password", this.changePasswordForm.controls.current_password.value);
      formData.append("new_password", this.changePasswordForm.controls.new_password.value);

      if (this.changePasswordForm.valid && this.passmessage == '') {
      this.changePasswordService.changepassword(formData).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if(resData.status_code == 200){
          this.router.navigate(['dashboard']).then(() => {
            this.toastr.success('', resData.message);
          });
        }else{
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });

    }
  }

}
